.footer{
    position: relative;
    &>hr{
        border: 1px solid var(--lightgray);
    }

    &--socials{
        padding: 1rem 2rem;
        display: flex;
        flex-direction: column;
        gap: 4rem;
        align-items: center;
        justify-content: center;
        height: 20rem;
        &-link{
            display: flex;
            gap: 4rem;
            &>img{
                width: 2rem;
                height: 2rem;
                cursor: pointer;
            }
        }
    }

    &--logo >img{
        width: 10rem;
    }
}

.blur-f1{
    bottom: 0;
    right: 15%;
    width: 26rem;
    height: 12rem;
    filter: blur(200px);
    background: red;  
}
.blur-f2{
    bottom: 0;
    left: 15%;
    width: 26rem;
    height: 12rem;
    filter: blur(200px);
    background: rgb(255, 115, 0);  
}