/* make variables */
:root {
    --lightgray: #D9D9D9;
    --gray: #9c9c9c;
    --orange: #f48915;
    --darkGrey: #464D53;
    --caloryCard: #656565;
    --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
    --appColor:  #3c3f45;
  }
  
  .App {
    background-image: url(data:image/png;base64,);
    mix-blend-mode: overlay;
    background-color: var(--appColor);
    display: flex;
    flex-direction: column;
    gap: 6rem;
    overflow: hidden;
  }
  ::-webkit-scrollbar{
    display: none;
  }

  .stroke{
    color: transparent;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #fff;
  }
  .btn{
    justify-content: center;
    align-items: center;
    flex-direction: row;
    background-color: #fff;

    padding: 0.5rem;
    font-weight: bold;
    border: 2px solid transparent;
    transition: all ease-in-out 0.3s;
  }
  .btn:hover{
    cursor: pointer;

  }

  .blur{
    background: rgba(255, 94, 0, 0.69);
    position: absolute;
    border-radius: 50%;
    filter: blur(212px);
    z-index: -9;
  }